.modal {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.25);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
}

.modal_content {
    position: relative;
    width: 50%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background-color: white;
    border-radius: 4px;
    padding: 20px;
    overflow: auto;
}

.note-circle {
    cursor: pointer;
}

.note-circle:hover {
    transform: scale(1.1);
}

@media screen and (max-width: 768px) {
    .modal_content {
        width: 80%;
    }
}